@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:wght@600&family=Raleway:ital,wght@0,100;0,700;1,300;1,900&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  background: #e4e4e4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container {
  width: 1280px;
  max-width: 100%;
  margin: 30px auto;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

.form-container {
    background-color: #99ccce;
    padding: 25px;
}

.display-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 80vh;
}

.email {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 18px;
    font-size: 20px;
}
.password {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 18px;
    font-size: 20px;
}

input[type='email'] {
    border: none;
    border: 1px solid inherit;
    box-shadow: white 0 -1px inset;
    background-color: inherit;
    margin-top: 5px;
    width: 220px;
    padding: 6px 8px; 
    font-size: 18px;
  
    font-family: 'Karla', sans-serif;
}

input[type='password'] {
    border: none;
    border: 1px solid inherit;
    box-shadow: white 0px -1px inset;
    background-color: inherit;
    margin-top: 5px;
    width: 220px;
    padding: 6px 8px; 
    font-size: 18px;
    font-family: 'Karla', sans-serif;
}

input[type='password'], input[type='email']:focus {
   outline: none;
}

::-webkit-input-placeholder {
    color: rgb(0, 0, 0);
    opacity: 1; /* Firefox */
  }

:-ms-input-placeholder {
    color: rgb(0, 0, 0);
    opacity: 1; /* Firefox */
  }

::placeholder {
    color: rgb(0, 0, 0);
    opacity: 1; /* Firefox */
  }

button[type='submit'] {
    width: 110px;
    padding: 5px;
    cursor: pointer;
    margin-top: 18px;
    margin-bottom: 12px;
    font-size: 16px;
    background-color: inherit;
    border: none;
    border: 1px solid white;
    color: rgb(0, 0, 0);
    font-family: 'Roboto', sans-serif;
}

.register {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
button[type='button'] {
    background-color:  inherit;
    border: none;
    cursor: pointer;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
}

h1 {
    font-size: 32px;
    font-family: 'Roboto', sans-serif;
}

.login-form-frame {
    border: none;
    border-top: 1px solid white;
    padding: 15px;
    margin-bottom: 20px;
}
.register-form-frame {
  
    border: none;
    border-top: 1px solid white;
    padding: 15px;
}
